import React, { useState, useEffect, useCallback } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
//import style from './style.css'
import { Button } from "@mui/material";
import Dialog from "@mui/material/Dialog";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";

function AcceptNewTasksBody(props) {
  let [items, setItems] = useState([]);
  let [openDialog, setOpenDialog] = useState(false);
  let [selectedTasks, setSelectedTasks] = useState();

  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  useEffect(() => {
    setOpenDialog(true);
    axiosInstance
      .get("projonline/gettasks/toBeAccepted", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setItems(res.data.tasks);
        setOpenDialog(false);
        return;
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const handleClick = (event, selectedTasks) => {
    //console.log(selectedTasks)
    props.history.push({
      pathname: "/acceptNewSpecificTask",
      data: selectedTasks,
    });
  };
  const columns = [
    { field: "taskRef", headerName: "Task ID", width: 250 },
    { field: "siteID", headerName: "Site ID", width: 130 },
    {
      field: "companyName",
      headerName: "Assigned Subcon / Vender",
      width: 290,
      valueGetter: (params) => params.row.assignedSubcon.companyName,
    },
    {
      field: "assignedMobitelOfficer",
      headerName: "Assinged  Officer",
      width: 250,
      valueGetter: (params) => params.row.assignedMobitelOfficer.name,
    },
    {
      field: "taskRaisedDate",
      headerName: "Initiated date",
      width: 180,
      valueGetter: (params) =>
        params.row.taskHistory
          .find((x) => x.taskStatus === "Subcon allocated")
          .performedDate.split("T")[0],
    },
  ];

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">
                <div className="col-sm-6">
                  <h1>All New Tasks</h1>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>

        <div style={{ height: 429, width: "100%" }}>
          <DataGrid
            getRowId={(item) => item.taskRef}
            rows={items}
            columns={columns}
            pageSize={10}
            rowsPerPageOptions={[10]}
            rowHeight={30}
            headerHeight={40}
            components={{ Toolbar: GridToolbar }}
            checkboxSelection
            onSelectionModelChange={(newSelection) => {
              var list = [];
              for (var i = 0; i < newSelection.length; i++) {
                list.push(
                  items.filter((obj) => obj["taskRef"] === newSelection[i])[0]
                );
              }
              setSelectedTasks(list);
              //console.log(newSelection)
              //console.log(selectedTasks)
            }}
          />

          <Button
            variant="contained"
            color="primary"
            onClick={(event) => {
              handleClick(event, selectedTasks);
            }}
          >
            To Assign
          </Button>
        </div>
      </div>
      <Dialog
        open={openDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(AcceptNewTasksBody);
